import { loadData } from '@/api/common'
import Scroll from '@/components/scroll'
import ConversionTips from './conversionTips'
import moment from 'moment'
import { imgUrl } from '@/api/url'
export default {
  components: {
    Scroll,
    ConversionTips
  },
  data() {
    return {
      imgUrl,
      isShow: true,
      conversion: true,
      listInfoDate: [],
      convert: {}
    }
  },
  created() {
    this.listDate()
  },
  methods: {
    listDate() {
      loadData('/member/gift/page').then(data => {
        this.listInfoDate = data.data
        for (let i = 0; i < data.data.length; i += 1) {
          data.data[i].createTime = moment(data.data[i].createTime).format(
            'YYYY-MM-DD'
          )
        }
        console.log(this.listInfoDate)
      })
    },
    toConversion(id, addressId, giftName) {
      this.convert = {
        id: id,
        addressId: addressId,
        giftName: giftName
      }
      // sessionStorage.setItem('addressId', id)
      this.$refs.ConversionTips.show()
    }
  }
}



